// const quizContainer = document.querySelector(".quiz__container");

class QuestionView {
  #data = [];
  parentEl = document.querySelector(".quiz__container");
  _question = document.querySelector(".quiz__question");
  _optionsContainer = document.querySelectorAll(".quiz__options");
  _btnNext = document.querySelector(".btn__next");
  _btnPrevious = document.querySelector(".btn__previous");
  _scoreEl = document.querySelector(".score");

  render(data) {
    this.#data = data;
    this.clear();
    const markup = this.generateMarkup();
    this.parentEl.insertAdjacentHTML("afterbegin", markup);
  }

  generateMarkup() {
    const [data] = this.#data;
    data.incorrectAnswer.push(data.correctAnswer);

    const markup = `
            <h6 class="quiz__question">${data.question}</h6>
            <ol type="a" class="quiz__options">
            ${data.incorrectAnswer.map((option) => {
              return `<li class="options">${option}</li>`;
            })}
            </ol>`;
    return markup;
  }

  clear() {
    this.parentEl.innerHTML = "";
  }

  checkAnswer(data) {
    document.querySelectorAll(".options").forEach((li) => {
      if (li.innerText === data[0].correctAnswer) {
        li.classList.add("right");
      }
    });

    this.parentEl.addEventListener("click", (e) => {
      const ans = e.target.closest(".options");
      if (!ans) return;

      const userAns = e.target.innerText;
      const correct = data[0].correctAnswer;

      if (userAns === correct) {
        e.target.classList.add("green");
        Number(++this._scoreEl.textContent) + 1;
      }

      if (userAns !== correct) {
        e.target.classList.add("wrong");

        document.querySelectorAll(".options").forEach((li) => {
          if (li.innerText === data[0].correctAnswer) {
            li.classList.add("correct");
          }
        });
      }
    });
  }

  renderSpinner() {
    const spinner = `<div class="spinner-border" role="status">
    <span class="visually-hidden">Loading...</span>
    </div>`;
    this.parentEl.insertAdjacentHTML("afterbegin", spinner);
  }

  addHandlerRender(handler) {
    this._btnNext.addEventListener("click", handler);
  }
}

export default new QuestionView();
