import * as model from "./model.js";
import view from "./view.js";
import questionView from "./view.js";

const controlQuiz = async function () {
  try {
    //Render Spinner
    questionView.renderSpinner();

    //Load question
    await model.loadMcq();

    //render question
    questionView.render(model.state.mcq);

    //Get Answer
    questionView.checkAnswer(model.state.mcq);

  } catch (err) {
    console.error(err);
  }
};

const init = function () {
  controlQuiz();
  view.addHandlerRender(controlQuiz);
};

init();
